//CONTAINER
.lodge__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #ff6060;
}

@media (max-width: 769px) {
  .lodge __container {
    flex-direction: column;
    align-items: flex-start;
  }
}

//BODY
.lodge__body {
  display: flex;
  flex-direction: column;
  width: 70%;
  &__title {
    font-size: 2em;
  }
  &__localisation {
    font-size: 1.2em;
    margin-top: 5px;
  }
  &__tags {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    color: white;
    margin: 20px 10px 24px 0;

    div {
      margin-right: 10px;
      height: 25px;
      margin-bottom: 10px;
      background: #ff6060;
      padding: 0px 20px;
      border-radius: 10px;
      display: flex;
      align-items: center;
    }
  }
  p {
    margin: unset;
    max-width: 50%;
  }
}

@media (max-width: 769px) {
  .lodge {
    &__container {
      display: flex;
      flex-direction: column;
    }
    &__body {
      width: 100%;
      &__title {
        font-size: 1.5em;
      }
      &__localisation {
        font-size: 1em;
        margin-top: 5px;
      }
      &__tags {
        margin: 10px 10px 10px 0;
        font-size: 0.7em;
      }
    }
  }
}
//HOST-PROFIL
.host {
  &__container {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  &-profil {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    &__name {
      text-align: end;
      margin-right: 8px;
      font-size: 1.2em;
      width: 50%;
    }
    &__img {
      border-radius: 50%;
      width: 64px;
      height: 64px;
    }
  }
  &-rating {
    &__stars {
      font-size: 2em;
      margin: 24px 0 34px 8px;
    }
  }
}

@media (max-width: 769px) {
  .host {
    &__container {
      flex-direction: row-reverse;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      margin-bottom: 20px;
    }
    &-rating {
      &__stars {
        margin-bottom: 24px;
        font-size: 1.2em;
        margin-left: 1px;
      }
    }
  }
}
