//GALERIE
.home-cards {
  //CONTAINER
  &__container {
    height: 829px;
    overflow: scroll;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: #f6f6f6;
    border-radius: 25px;
    padding: 50px 56px;
    &:after {
      content: "";
      flex: 0 1 calc(33% - 2em);
    }
  }
  //CARD
  &__card {
    min-height: 250px;
    margin-bottom: 50px;
    position: relative;
    border: 0px;
    padding: 0px;
    border-radius: 10px;
    flex: 0 1 calc(33% - 2em);
    transition: transform 0.3s ease;
    img {
      object-fit: cover;
      border-radius: 10px;
      width: 100%;
      height: 100%;
    }
    &:after {
      border-radius: 10px;
      height: 100%;
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      background: linear-gradient(
        180deg,
        rgba(255, 255, 255, 0) 0%,
        rgba(0, 0, 0, 0.5) 100%
      );
    }
    &:hover {
      transform: scale(1.1);
    }
  }
  &__title {
    position: absolute;
    bottom: 0.5rem;
    left: 0.2rem;
    color: #ffffff;
    font-size: 1.3em;
    margin: 5px;
    line-height: 142.6%;
    z-index: 1;
    word-wrap: break-word;
  }
}

// MEDIA QUERIES
@media (max-width: 769px) {
  .home-cards {
    &__container {
      padding: 0px;
      background-color: white;
    }
    &__card {
      margin-bottom: 20px;
      flex: none;
      width: 100%;
      max-height: 255px;
    }
  }
}
